@import url(https://fonts.googleapis.com/css?family=Proxima+Nova);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --light-blue-40: #f8f9fc;
    --light-blue-60: #eef2fa;
    --light-blue-80: #d9e3fa;
    --light-blue-100: #c3d3fa;

    --light-shadow: #f1f1f1;
    --menu-shadow: #b3b1b160;
    --dark-shadow: rgb(104, 104, 104);

    --light-grey-100: #cfd4d9;
    --light-grey-80: #dfe2e6;
    --light-grey-60: #eaecef;

    --dark-grey-100: #222529;
    --dark-grey-80: #353a3f;
    --dark-grey-40: #6e757c;

    --iris-60: #a5a6f6;
    --iris-80: #7879f1;
    --iris-100: #5d5fef;

    --accent-pink-100: #df5571;
}

.body {
    background-color: #eef2fa;
    background-color: var(--light-blue-60);
    position: absolute;
    width: 100%;
    min-height: 100%;
    font-family: "proxima-nova", sans-serif;
    font-size: 16px;
}

nav {
    left: 0;
    top: 200;
    width: 100%;
    text-align: center;
    padding-top: 20px;
}

footer {
    position: fixed;
    height: 30px;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: white;
    text-align: center;
    padding-top: 5px;
}

.auth-form {
    width: 80%;
    max-width: 535px;
    padding: 0;
    display: block;
    margin: 80px auto;
}

.invisible-button {
    visibility: hidden;
}

button {
    padding: 12px 0px;
    width: 150px;
    border: none;
}

.primary-button {
    background-color: #a5a6f6;
    background-color: var(--iris-60);
    font-size: 14px;
    color: white;
    border-radius: 5px;
    box-shadow: 5px 5px 25px #f1f1f1;
    box-shadow: 5px 5px 25px var(--light-shadow);
}

.primary-button:hover:enabled {
    background-color: #7879f1;
    background-color: var(--iris-80);
    -webkit-animation: primaryButtonHover 1s;
            animation: primaryButtonHover 1s;
}

@-webkit-keyframes primaryButtonHover {
    from {
        background-color: #a5a6f6;
        background-color: var(--iris-60);
    }
    to {
        background-color: #7879f1;
        background-color: var(--iris-80);
    }
}

@keyframes primaryButtonHover {
    from {
        background-color: #a5a6f6;
        background-color: var(--iris-60);
    }
    to {
        background-color: #7879f1;
        background-color: var(--iris-80);
    }
}

button:disabled {
    background-color: #cfd4d9;
    background-color: var(--light-grey-100);
    box-shadow: none;
}

.secondary-button {
    background-color: #d9e3fa;
    background-color: var(--light-blue-80);
    font-size: 14px;
    color: white;
    border-radius: 5px;
    box-shadow: 5px 5px 25px #f1f1f1;
    box-shadow: 5px 5px 25px var(--light-shadow);
}

.secondary-button:hover:enabled {
    background-color: #c3d3fa;
    background-color: var(--light-blue-100);
    -webkit-animation: secondaryButtonHover 0.5s;
            animation: secondaryButtonHover 0.5s;
}

@-webkit-keyframes secondaryButtonHover {
    from {
        background-color: #d9e3fa;
        background-color: var(--light-blue-80);
    }
    to {
        background-color: #c3d3fa;
        background-color: var(--light-blue-100);
    }
}

@keyframes secondaryButtonHover {
    from {
        background-color: #d9e3fa;
        background-color: var(--light-blue-80);
    }
    to {
        background-color: #c3d3fa;
        background-color: var(--light-blue-100);
    }
}

.button-row {
    margin-top: 60px;
    width: 100%;
    text-align: right;
    display: flex;
    justify-content: space-between;
}

h3 {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    color: #353a3f;
    color: var(--dark-grey-80);
    font-weight: bold;
}

.groups-area {
    padding: 0px 20px;
}

.group-area {
    margin-top: 20px;
}

.topContainer {
    -webkit-column-count: 3;
            column-count: 3;
    display: flex;
    flex-direction: row;
    place-items: center start;
    flex-wrap: wrap;
}

.rcorners {
    border-radius: 5px;
    background: #fff;
    padding: 10px;
    margin: 5px 15px 5px 0px;
    box-shadow: 5px 5px 25px var(--light-shadow);
}

.contact-card-area {
    display: flex;
    flex-direction: row;
    place-items: start start;
    flex-wrap: wrap;
}

.group-contact-card {
    min-width: 380px;
    width: calc((100vw - 70px)/3.1);
}

.group-contact-card:hover {
    background-color: var(--light-grey-80);
    cursor: pointer;
}

.group-contact-card:hover th {
    background-color: var(--light-grey-60);
}

.group-contact-card:hover td{
    background-color: var(--light-grey-60);
}

@media (max-width: 1025px) {
    .group-contact-card {
        min-width: calc((100vw - 70px)/2);
    }
}

@media (max-width: 961px) {
    .group-contact-card {
        min-width: calc((100vw - 40px)/2.1);
    }
}

@media (max-width: 641px) {
    .group-contact-card {
        min-width: calc(100vw - 40px);
    }
}

.contact-card-title {
    display: flex;
    justify-content: space-between;
}

.contact-card-title > .left-col {
    display: flex;
    place-items: center start;
}

.dot {
    height: 30px;
    width: 30px;
    background-color: #c3d3fa;
    border-radius: 50%;
    display: inline-block;
    text-align: center;
    margin-right: 15px;
}

.padded {
    margin-left: 20px;
}

.space {
    margin-top: 5px;
}

.centeredInDot {
    margin-top: 2.5px;
    color: #7879f1;
}

.subText {
    background: var(--light-blue-40);
}

.title {
    color: #222529;
    font-weight: bold;
    font-size: 26pt;
}

.lb100 {
    background-color: #c3d3fa;
}

th,
td {
    background-color: var(--light-blue-40);
}

th {
    font-weight: normal;
    color: var(--iris-80);
    width: 35%;
    text-align: center;
}

td {
    padding-left: 10px;
    color: var(--dark-grey-80);
}

table {
    width: 100%;
    margin-top: 5px;
}

table,
th,
td {
    border: 1px solid white;
    border-collapse: collapse;
    font-size: 14px;
}

.starred {
    color: var(--accent-pink-100);
}

.unstarred {
    color: var(--iris-60);
}

.contact-star:hover {
    -webkit-animation: starHover 1s;
            animation: starHover 1s;
    cursor: pointer;
    color: var(--iris-80);
}

@-webkit-keyframes starHover {
    from {
        color: var(--iris-60);
    }
    to {
        color: var(--iris-80);
    }
}

@keyframes starHover {
    from {
        color: var(--iris-60);
    }
    to {
        color: var(--iris-80);
    }
}

.trigger-text {
    color: var(--dark-grey-40);
}

.trigger-text:hover {
    text-decoration: underline;
}
input,
label {
    display: block;
}

.input-field {
    margin: 25px 0px;
}

.custom-input-field {
    -webkit-animation: growDown 0.2s ease-in forwards;
            animation: growDown 0.2s ease-in forwards;
    -webkit-transform-origin: top center;
            transform-origin: top center;
}

input {
    width: 100%;
    padding: 10px 16px;
    margin: 5px 0;
    border: 1px solid var(--light-blue-60);
    border-radius: 5px;
    box-shadow: 5px 5px 25px var(--light-shadow);
    outline: none;
}

input::-webkit-input-placeholder {
    color: var(--light-grey-100);
    font-style: italic;
}

input:-ms-input-placeholder {
    color: var(--light-grey-100);
    font-style: italic;
}

input::placeholder {
    color: var(--light-grey-100);
    font-style: italic;
}

.input-error {
    color: red;
    font-size: 12px;
    margin: 5px 5px;
}

label {
    font-size: 14px;
    color: var(--dark-grey-40);
    padding-left: 5px;
}

.custom-input-label {
    background-color: transparent;
    padding: 0;
    margin: 0;
    font-size: 14px;
    color: var(--light-grey-40);
    border: none;
}

.custom-input-label {
    height: 21px;
}

.custom-input-label:focus {
    background-color: var(--light-blue-40);
}

.label {
    display: flex;
    justify-content: space-between;
}

.delete-button {
    color: var(--dark-grey-40);
    font-size: 14px;
}

.delete-button:hover {
    cursor: pointer;
    color: var(--iris-100);
}

.split{
    display: flex;
    flex-direction: column;
    margin: 0px;
    padding: 0;
    line-height: 0px;
}
.edit-contact-modal {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.edit-contact-card {
    background-color: var(--light-blue-60);
    padding: 20px;
    width: 60%;
    border-radius: 5px;
    box-shadow: 5px 5px 25px var(--dark-shadow);
    max-height: 80vh;
    overflow-y: scroll;
}

.form-area {
    padding: 15px;
    overflow-y: scroll;
    max-height: 60vh;
}

.large-profile-initials {
    height: 100px;
    width: 100px;
    background-color: #c3d3fa;
    border-radius: 50%;
    display: inline-block;
    text-align: center;
    margin-right: 15px;
}

.edit-contact-row {
    text-align: right;
}

@media (max-width: 961px){
    .edit-contact-card {
        width: 80%;
    }
}

@media (max-width: 481px){
    .edit-contact-card {
        width: 90%;
    }
}
.profile-content {
    padding: 35px 25px;
}

.form,
.title-row {
    display: flex;
}

.info-dropdown {
    height: 1.75rem;
}

.info {
    position: relative;
    margin-left: 5px;
    margin-top: 12px;
    font-size: 14px;
    color: var(--dark-grey-40);
}

.info:hover {
    text-decoration: underline;
    color: var(--iris-100);
}

.info:hover + .tool-tip {
    display: block;
}

.col {
    width: 535px;
    padding: 0;
}

.left-col {
    padding-right: 12.5px;
}

.right-col {
    padding-left: 12.5px;
}

@media (max-width: 840px) {
    .form {
        display: block;
    }

    .col {
        width: 100%;
    }

    .left-col {
        padding-right: 0;
    }

    .right-col {
        padding-left: 0;
    }
}

.form button {
    position: relative;
    right: 0;
}

.profile-photo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    box-shadow: 5px 5px 25px var(--light-shadow);
}

.profile-photo:hover {
    background-color: black;
    cursor: pointer;
}

img:hover {
    opacity: 0.7;
}

img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 2px solid var(--light-blue-100);
}

.profile-photo-row {
    width: 100%;
    text-align: center;
    padding: 15px 0px;
    justify-content: center;
    display: flex;
}

.large-profile-initials:hover {
    opacity: 0.8;
}

.large-initials {
    height: 100px;
    width: 100px;
    color: #7879f1;
    font-size: 50px;
    line-height: 90px;
}

.overlay-icon {
    display: none;
    color: white;
    opacity: 0.8;
    font-size: 50px;
    position: relative;
    top: -50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

img:hover + .overlay-icon {
    display: block;
}

.new-field-button {
    color: var(--dark-grey-40);
}

.new-field-button:hover {
    cursor: pointer;
    color: var(--dark-grey-80);
}

header {
    top: 0;
    padding: 15px 15px;
    background: rgb(255, 255, 255);
    background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 1) 0%,
        rgba(255, 255, 255, 0) 100%
    );
}

.header-content {
    padding-left: 40px;
    display: flex;
    background-color: white;
    height: 50px;
    padding: 10px 15px;
    border-radius: 25px;
    justify-content: space-between;
    box-shadow: 5px 5px 25px var(--light-shadow);
}

.left-header a {
    font-size: 26px;
    line-height: 30px;
}

.right-header {
    display: flex;
}

.icon {
    font-size: 26px;
    line-height: 26px;
    color: var(--light-blue-100);
    border-radius: 15px;
    margin-right: 30px;
}

.icon.on {
    color: var(--iris-80);
}

.profile-icon {
    width: 30px;
    height: 30px;
    border-radius: 15px;
    border: 2px solid var(--light-blue-100);
}

.icon:hover {
    cursor: pointer;
    color: var(--iris-80);
    -webkit-animation: textColorChange 1s;
            animation: textColorChange 1s;
}

.icon.off:active,
.icon.on:active {
    transition: opacity 0.5s, -webkit-transform 0.5s;
    transition: opacity 0.5s, transform 0.5s;
    transition: opacity 0.5s, transform 0.5s, -webkit-transform 0.5s;
    -webkit-transform: rotate(-180deg) scale(0.5);
            transform: rotate(-180deg) scale(0.5);
    opacity: 0;
}

.off:after,
.on:after {
    transition: opacity 0.5s, -webkit-transform 0.5s;
    transition: opacity 0.5s, transform 0.5s;
    transition: opacity 0.5s, transform 0.5s, -webkit-transform 0.5s;
    -webkit-transform: scale(0.5) rotate(-180deg) scale(1);
            transform: scale(0.5) rotate(-180deg) scale(1);
    opacity: 1;
}

.profile-icon.on {
    border: 2px solid var(--iris-80);
}

@-webkit-keyframes textColorChange {
    from {
        color: var(--iris-60);
    }
    to {
        color: var(--iris-80);
    }
}

@keyframes textColorChange {
    from {
        color: var(--iris-60);
    }
    to {
        color: var(--iris-80);
    }
}

.profile-icon:hover {
    cursor: pointer;
    border: 2px solid var(--iris-80);
    -webkit-animation: borderColorChange 1s;
            animation: borderColorChange 1s;
}

@-webkit-keyframes borderColorChange {
    from {
        border: 2px solid var(--iris-60);
    }
    to {
        border: 2px solid var(--iris-80);
    }
}

@keyframes borderColorChange {
    from {
        border: 2px solid var(--iris-60);
    }
    to {
        border: 2px solid var(--iris-80);
    }
}

.profile-dropdown {
    text-align: right;
    width: 30px;
}

.profile-dropdown > .dropdown {
    margin: 10px -180px;
}

.notification-dropdown > .dropdown {
    margin: 12px -180px;
}

.dropdown {
    font-size: 14px;
    border-radius: 5px;
    background-color: white;
    width: 200px;
    text-align: left;
    z-index: 1;
    box-shadow: 0px 0px 10px var(--menu-shadow);
    -webkit-animation: growDown 300ms ease-in-out forwards;
            animation: growDown 300ms ease-in-out forwards;
    -webkit-transform-origin: top center;
            transform-origin: top center;
    color: var(--dark-grey-40);
}

@-webkit-keyframes growDown {
    0% {
        -webkit-transform: scaleY(0);
                transform: scaleY(0);
    }
    80% {
        -webkit-transform: scaleY(1.1);
                transform: scaleY(1.1);
    }
    100% {
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
    }
}

@keyframes growDown {
    0% {
        -webkit-transform: scaleY(0);
                transform: scaleY(0);
    }
    80% {
        -webkit-transform: scaleY(1.1);
                transform: scaleY(1.1);
    }
    100% {
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
    }
}

.dropdown a {
    color: var(--dark-grey-40);
}

.dropdown a:hover {
    text-decoration: none;
}

ul {
    list-style-type: none;
    padding-left: 0;
}

li {
    padding: 10px 15px;
}

.inactive {
    display: none;
}

li:hover {
    cursor: pointer;
    background-color: var(--light-blue-40);
    color: var(--iris-80);
}

.popularity {
    background: var(--light-blue-100);
    width: 96px;
    height: 30px;
    text-align: center;
    font-size: 14px;
    vertical-align: middle;
    line-height: 30px;
    color: white;
    border-radius: 15px;
    box-shadow: 5px 5px 25px var(--light-shadow);
}

.popularity:hover {
    -webkit-animation: colorChange 1s;
            animation: colorChange 1s;
    background-color: var(--iris-80);
}

.popularity:hover + .tool-tip {
    display: block;
}

@media (max-width:481px) {
    .popularity {
        display: none;
    }
}

@-webkit-keyframes colorChange {
    from {
        background-color: var(--iris-60);
    }
    to {
        background-color: var(--iris-80);
    }
}

@keyframes colorChange {
    from {
        background-color: var(--iris-60);
    }
    to {
        background-color: var(--iris-80);
    }
}

.tool-tip {
    display: none;
    font-size: 12px;
    width: 200px;
    text-align: left;
    background-color: var(--light-blue-40);
    padding: 5px 10px;
    color: var(--dark-grey-40);
    border-radius: 5px;
    box-shadow: 0px 0px 10px var(--menu-shadow);
    border: 1px solid var(--light-grey-80);
    position: relative;
    z-index: 1;
}

.popularity-dropdown {
    text-align: right;
    width: 96px;
    margin-right: 30px;
}

/* Person page styling */
.person {
    justify-content: space-between;
    width: 97%;
    padding: 0.5em 2em;
    border-radius: 90px;
    margin: 10px 20px;
}

.person div {
    margin: auto;
}

.color-0 { background-color: white; }

.color-1 { background-color: #dde8fd; }

.w-15 { width: 15%; }
.w-10 { width: 10%; }
.w-5 { width: 30px; }
.w-2 { width: 30px; }

.contact-card {
    height: 200px;
    width: 400px;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
    border: 3px solid green;
    background: gray;
}

.initials {
    background-color: #c3d3fa;
    color: #5d5fef;
    padding: 5px;
    border-radius: 90%;
    text-align: center;
}
.options-bar {
    padding: 19px 20px;
    display: flex;
    justify-content: space-between;
}

.right-items {
    display: flex;
    position: relative;
}

#add-contact-button-small {
    display: none;
}

@media (max-width: 480px) {
    #add-contact-button {
        display: none;
    }

    #add-contact-button-small {
        display: block;
    }
}

@media (max-width: 481px) {
    .button {
        width: 50px;
    }
}

.two-select-button {
    display: flex;
    width: 220px;
    text-align: center;
    font-size: 14px;
    border: 1px solid var(--light-grey-80);
    border-radius: 5px;
    box-shadow: 5px 5px 25px var(--light-shadow);
}

.two-select-button a {
    text-decoration: none;
    color: var(--dark-grey-100);
}

.left {
    padding: 10px 0px;
    width: 50%;
    border-radius: 5px 0px 0px 5px;
}

.right {
    padding: 10px 0px;
    width: 50%;
    border-radius: 0px 5px 5px 0px;
}

.selected {
    background-color: white;
    cursor: default;
}

.deselected {
    background-color: var(--light-grey-60);
}

.deselected:hover {
    background-color: var(--light-grey-80);
    cursor: pointer;
}
.search-bar {
    align-items: center;
    display: flex;
    box-shadow: 5px 5px 25px var(--light-shadow);
    border-radius: 5px;
    border: 1px solid var(--light-blue-60);
    width: 400px;
    margin-right: 25px;
    z-index: 0;
}

.search-bar input{
    border-radius: 5px 0px 0px 5px;
    box-shadow: none;
    border: none;
}

.search-button {
    border-radius: 0px 5px 5px 0px;
    padding: 10px 20px;
    background-color: var(--light-blue-80);
    color: white;
}

.search-button:hover {
    cursor: pointer;
    background-color: var(--light-blue-100);
    -webkit-animation: secondaryButtonHover 0.5s;
            animation: secondaryButtonHover 0.5s;
}

